import React, { Component } from 'react';
import './App.css';
import { Route, Switch } from 'react-router-dom';
import Team from './Team';
import SingleTeamMember from './SingleTeamMember';
import generateMember from "./helper";
import TeamData from './TeamData';

class App extends Component {
  findMember(id){
    return TeamData.find(function(member){
      return member.id === id
    });
  }
  render(){
    return (
      <Switch>
      <Route 
      exact 
      path ="/team/:id"
      render={(routerProps)=><SingleTeamMember member={generateMember(this.findMember(routerProps.match.params.id))}/>}
      />
      <Route
      exact
      path ="/team"
      render = {()=><Team />}
      />
      </Switch>
    );
  }
}

export default App;
