import React, { Component } from 'react';
import TeamData from "./TeamData";
import TeamMember from './TeamMember';
import "./Team.css"

 class Team extends Component {
    render() {
        const Data = TeamData.map(member => (
            <TeamMember key={member.id} name={member.name} src={member.img} id={member.id} role={member.role}/>
        ) )
        return (
            <div className = "Team">
                {Data}
            </div>
        )
    }
}

export default Team
